<template>
  <div :style="backgroundStyle">
    <div class="navBar">
      <span @click="back" class="mdui-ripple"><i class="mdui-icon material-icons">&#xe5c4;</i></span>
      <span>{{ id }}</span>
      <span></span>
    </div>
    <div class="bottom-bar">
      <input type="text" v-model="message" placeholder="请输入文本">
      <div class="mdui-btn mdui-color-blue mdui-text-color-white send-btn" @click="send">发送</div>
    </div>
    <div class="talk-lists" ref="talk-lists">
      <div class="talk" v-for="(item, index) in talks" :key="index">
        <div v-if="item.from === 'self'" class="talk-self"><span class="talk-content ">{{ item.data }}</span><img src="../assets/temp/avatar1.jpg" alt=""></div>
        <div v-if="item.from !== 'self'" class=" talk-unself"><img src="../assets/temp/avatar1.jpg" alt=""><span class="talk-content">{{ item.data }}</span></div>
      </div>
      <div style="height: 60px"></div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'talk',
    data () {
      return {
        message: null,
        talks: []
      }
    },
    mounted () {
      let _this = this
      document.body.onscroll = function () {
        // console.log(document.documentElement.scrollTop)
      }

      this.$_event.removeEvent('onTextMessage')
      this.$_event.addEvent('onTextMessage', (res) => {
        console.log(res)
        if (res.from === _this.id) {
          _this.talks.push(res)
        }
      })
    },
    computed: {
      id () {
        return this.$route.params.id
      },
      backgroundStyle () {
        console.log(window.document.documentElement.clientHeight)
        return 'min-height: '+ (document.documentElement.clientHeight - 89) + 'px;background-color: #f3f3f3;padding-top: 44px;padding-bottom: 45px;'
      }
    },
    methods: {
      back () {
        this.$router.back()
      },
      send () {
        let _this = this
        if (!this.message) {
          this.$_mdui.snackbar({
            message: '不能发送空消息',
            position: 'top'
          });
          return
        }
        var id = this.$_WebIM_conn.getUniqueId();                 // 生成本地消息id
        var msg = new WebIM.message('txt', id);      // 创建文本消息
        msg.set({
          msg: this.message,                  // 消息内容
          to: this.id,                          // 接收消息对象（用户id）
          roomType: false,
          success: function (id, serverMsgId) {
            _this.talks.push({
              data: _this.message,
              error: false,
              errorCode: '',
              errorText: '',
              ext: {},
              from: 'self',
              sourceMsg: _this.message,
              type: 'chat',
            })
            // _this.message = null
            console.log('发送成功')
          },
          fail: function(e){
            _this.$_mdui.snackbar({
              message: '发送失败-请稍后重试',
              position: 'top'
            });
          }
        });
        msg.body.chatType = 'singleChat';
        this.$_WebIM_conn.send(msg.body);
      }
    },
    watch: {
      talks () {
        let hei = document.documentElement.offsetHeight + 60
        window.scrollTo(0, hei)
      }
    }
  }
</script>

<style scoped>
.navBar {
  background-color: #2196f3;
  color: white;
  font-weight: 400;
  font-size: 18px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 10px 15px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, .3);
}
  .bottom-bar {
    position: fixed;
    left: 0;
    bottom: 0;
    right: 0;
    height: 45px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    background-color: white;
  }
  .bottom-bar .send-btn {
    height: 100%;
    line-height: 45px;
  }
  .bottom-bar input {
    flex: 1;
    height: 21px;
    padding: 11px 10px;
    border: 1px solid gainsboro;
    outline: none;
  }
  .talk-lists {
    background-color: #f3f3f3;
  }
  .talk-lists .talk {
    padding: 10px;
    min-height: 40px;
    line-height: 20px;
  }
  .talk-lists .talk .talk-content {
    background-color: white;
    border-radius: 5px;
    max-width: 70%;
    width: auto;
    display: inline-block;
    padding: 10px 20px;
    white-space: pre-wrap;
  }
.talk-lists .talk .talk-self {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: flex-start;
}
.talk-lists .talk img {
  width: 40px;
  height: 40px;
  border-radius: 80px;
}
.talk-lists .talk .talk-self img {
  margin-left: 10px;
}
.talk-lists .talk .talk-unself img {
  margin-right: 10px;
}
.talk-lists .talk .talk-self .talk-content {
  background-color: #2196f3;
  color: white;
}
.talk-lists .talk .talk-unself {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
}
</style>



